import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/kelas",
    name: "Classes",
    component: function () {
      return import('../views/classes/Classes.vue')
    }
  },
  {
    path: "/kelas/id",
    name: "ClassDetail",
    component: function () {
      return import('../views/classes/ClassDetail.vue')
    }
  },
  {
    path: "/login",
    name: "Login",
    component: function () {
      return import('../views/auth/Login.vue')
    }
  },
  {
    path: "/register",
    name: "Register",
    component: function () {
      return import('../views/auth/Register.vue')
    }
  },
  {
    path: "/beranda",
    name: "Dashboard",
    component: function () {
      return import('../views/dashboard/Dashboard.vue')
    }
  },
  {
    path: "/kelas-saya",
    name: "MyClass",
    component: function () {
      return import('../views/dashboard/MyClass.vue')
    }
  },
  {
    path: "/invoice-dan-pembayaran",
    name: "Invoice",
    component: function () {
      return import('../views/dashboard/Invoice.vue')
    }
  },
  {
    path: "/pengaturan-akun",
    name: "Account",
    component: function () {
      return import('../views/dashboard/Account.vue')
    }
  },
  {
    path: "/akses-kelas",
    name: "AccessClass",
    component: function () {
      return import('../views/dashboard/AccessClass/AccessClass.vue')
    }
  },
  // {
  //   path: "/about",
  //   name: "About",
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () =>
  //     import(/* webpackChunkName: "about" */ "../views/About.vue")
  // }
];

const router = new VueRouter({
  routes,
});

export default router;
